* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Open Sans";
}

.Navbar {
    background-color: white;
    height: 10vh;
    width: 100vw;
    padding: 0vw 3vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.2rem;
    border-bottom: 3px solid #f7f6fa;
}

.Navbar .Right {
    width: 15vw;
    display: flex;
    justify-content: space-between;
}

.Navbar .Right span {
    cursor: pointer;
}

.Navbar .Right span:hover {
    color: #785dfb;
}

.Home,
.Visualization {
    background-color: white;
    height: 90vh;
    width: 100vw;
}

.Toast {
    z-index: 1;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: center;
    top: 20px;
    animation: toast-in 0.25s linear;
}

.Toast > div {
    text-align: center;
    color: white;
    padding: 0.5em;
    padding-left: 2em;
    padding-right: 2em;
    min-width: 30vw;
    max-width: 90vw;
    font-size: 1.5rem;
    border-radius: 100px;
}

@keyframes toast-in {
    0% {
        opacity: 0;
        top: 0px;
    }
    100% {
        opacity: 1;
        top: 20px;
    }
}

.Success > div {
    background-color: #00cc7d;
}

.Error > div {
    background-color: #ea4435;
}

.Home {
    display: grid;
    grid-template-columns: 6fr 4fr;
    grid-template-rows: auto;
}

.Home .Right {
    background-color: white;
    border-left: 3px solid #f7f6fa;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Home .Right form {
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

input {
    font-size: 1rem;
    background-color: #f7f6fa;
    border: 1px solid #f7f6fa;
    padding: 0.5em;
    padding-left: 2em;
    padding-right: 2em;
    margin-top: 0.5em;
    margin-bottom: 1.5em;
    border-radius: 100vw;
    cursor: pointer;
    width: 260px;
}

input:focus {
    outline: none;
    border: 1px solid #785dfb;
}

label {
    font-size: 1.2rem;
    cursor: pointer;
}

button {
    font-size: 1rem;
    color: white;
    background-color: #785dfb;
    border: 1px solid #785dfb;
    text-align: center;
    padding: 0.5em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 100vw;
    cursor: pointer;
}

button:hover {
    color: #785dfb;
    background-color: white;
}

h2 {
    color: #785dfb;
}

.Home .Left {
    overflow: scroll;
}

.Home .Left .Top {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 1em;
    border-bottom: 3px solid #f7f6fa;
}

.Home .Left .Top label {
    font-size: 1rem;
}

select {
    font-size: 1rem;
    border: none;
    text-align: center;
    border-radius: 100vw;
    cursor: pointer;
}

select:hover {
    color: #785dfb;
}

select:focus {
    outline: none;
}

.Home .Left .Bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 50px;
}

.GroupHeader {
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin-top: 1em;
    margin-bottom: 1em;
}

.GroupHeader .content {
    border: 1px solid #785dfb;
    padding: 0.5em 2em;
    border-radius: 100vw;
    background-color: white;
    z-index: 1;
    color: #785dfb;
}

.GroupHeader .line {
    width: 100%;
    height: 1px;
    position: absolute;
    background-color: #785dfb;
    border-radius: 100vw;
}

.GroupedCheckin {
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 0.5fr 1fr 1.5fr 0.25fr;
    grid-template-rows: auto;
}

.GroupedCheckin span {
    background-color: #f7f6fa;
    border: 1.5px solid white;
    padding: 1em;
    text-align: center;
    font-size: 0.9rem;
}

.Labels span {
    color: #785dfb;
    background-color: white;
}

.Delete {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    cursor: pointer;
}

.Delete:hover {
    background-color: rgb(120, 93, 251, 0.2);
}

.Visualization {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Bar {
    width: 1000px;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
